.section-frame1 {
    max-width: 1792px;
    margin: 0 auto;
    margin-bottom: 30px;
    overflow: hidden;

    .box-content {
        background: #F2EFE7;
        padding: 170px 6.6% 100px 17.86%;
        width: 54.17%;
        position: relative;
        font-family: var(--font1);

        &::before {
            content: "";
            background: url(images/graphics/bg-hanhtrinh.png) no-repeat;
            height: 89px;
            position: absolute;
            width: 900px;
            top: 0;
            left: 0;
        }

        label {
            color: #191919;
            font-size: 14px;
            line-height: 115%;
            letter-spacing: 0.24em;
            margin-bottom: 23px;
            display: block;
            text-transform: uppercase;
        }

        .title {
            color: #191919;
            font-size: 64px;
            margin-bottom: 28px;
            line-height: 1.2;
        }

        p {
            color: #6B6B6B;
            font-size: 16px;
            line-height: 160%;
            margin-bottom: 28px;
            font-family: Arial;
        }
    }

    .box-img {
        width: 45.83%;
        position: relative;

        img {
            width: 100%;
        }


        .swiper-slide {
            &::before {
                content: "";
                height: 80px;
                background: #fff;
                position: absolute;
                bottom: 0;
                width: 100%;
                z-index: 2;
            }

            h3 {
                position: absolute;
                left: 40px;
                bottom: 25px;
                font-size: 16px;
                color: #6B6B6B;
                font-family: Arial;
                z-index: 3;
                width: calc(100% - 320px);
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }
        }

        .swiper-pagination {
            font-family: var(--font1);
            color: #AA7D54;
            font-size: 24px;
            width: 80px;
            right: 200px;
            left: inherit;

            .swiper-pagination-current {
                font-size: 40px;
                color: #AA7D54;
            }

            .swiper-pagination-total {
                font-size: 25px;
                color: #AA7D54;
                opacity: 0.5;
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            width: 80px;
            height: 80px;
            line-height: 80px;
            bottom: 0;
            top: inherit;
            background: #AA7D54;

            svg {
                height: 34px;
                width: 34px;
                color: #fff;
            }
        }

        .swiper-button-prev {
            right: 80px;
            left: inherit;
            transform: rotate(180deg);
        }

        .swiper-button-prev.swiper-button-disabled,
        .swiper-button-next.swiper-button-disabled {
            opacity: 1;
            background: #F2EFE7;

            svg {
                color: #AA7D54
            }
        }
    }

    @media (max-width: 1791px) {
        .box-content {
            padding: 100px 6.6% 50px 17.86%;

            .title {
                font-size: 48px;
                margin-bottom: 20px;
            }
        }
    }

    @media (max-width: 1100px) {
        .box-content {
            padding: 50px 20px;
            width: 40%;

            .title {
                font-size: 35px;
            }
        }

        .box-img {
            width: 60%;
        }
    }

    @media (max-width: 979px) {
        .box-content p {
            display: none
        }

        .box-img {
            .swiper-slide {
                &::before {
                    height: 50px;
                }

                h3 {
                    left: 10px;
                    bottom: 12px;
                    width: calc(100% - 180px);
                }
            }

            .swiper-pagination {
                font-size: 16px;
                width: 50px;
                height: 50px;
                right: 110px;
                bottom: 0;

                .swiper-pagination-current {
                    font-size: 30px
                }

                .swiper-pagination-total {
                    font-size: 16px
                }
            }

            .swiper-button-prev,
            .swiper-button-next {
                width: 50px;
                height: 50px;
                line-height: 50px;

                svg {
                    height: 26px;
                    width: 26px;
                }
            }

            .swiper-button-prev {
                right: 50px;
            }
        }
    }

    @media (max-width: 576px) {
        justify-content: center;
        text-align: center;
        padding-bottom: 20px;
        margin-bottom: 0;

        .box-content {
            width: 100%;
            padding: 10px;
            background: #f7f4f0;

            &::before {
                display: none
            }

            label {
                color: #AA7D54;
                margin-bottom: 5px;
            }

            .title {
                font-size: 56px;
                margin-bottom: 10px;
            }
        }

        .box-img {
            width: 100%;
            margin-bottom: 20px;
            padding: 0 15px;

            .swiper-slide {
                background: #fff;
                padding: 8px;

                h3 {
                    width: 100%;
                }
            }

            .swiper-pagination {
                height: 30px;
                bottom: 50px;
                left: 0;
                right: 0;
                width: 100%;

                .swiper-pagination-bullet {
                    margin: 0 3px;
                    border-radius: 50%
                }
            }

            .swiper-button-prev,
            .swiper-button-next {
                width: 40px;
                height: 80px;
                line-height: 80px;
                margin-top: -60px;
                top: 50%;
                background: rgba(242, 239, 231, 0.7);
                opacity: 1 !important;
                right: 23px;

                svg {
                    height: 24px;
                    width: 24px;
                    color: #000;
                }
            }

            .swiper-button-prev {
                left: 23px;
            }
        }

    }
}

.section-frame2 {
    margin-bottom: 50px;

    .item-news {
        width: 440px;
        border: 1px solid #191919;
        margin: 0 40px;
        margin-bottom: 40px;
        margin-top: 60px;
        float: left;

        &::before {
            content: "";
            border: 1px solid #AA7D54;
            position: absolute;
            top: 10px;
            bottom: -10px;
            right: -10px;
            left: 10px;
        }

        .wrap {
            background: #fff;
            padding: 18px;
            position: relative
        }

        .thumb-art {
            width: 110px;
            margin-right: 26px;
            position: relative;

            &::before {
                content: "";
                background: url(images/graphics/book.png);
                background-size: cover;
                position: absolute;
                top: -5px;
                right: -10px;
                bottom: -5px;
                left: -5px;
            }
        }

        .description {
            font-size: 14px;
        }

        .line {
            background: #E7D5C5;
            height: 130px;
            width: 1px;
            position: absolute;
            bottom: -160px;
            left: 100px;

            &::after {
                content: "";
                background: #E7D5C5;
                height: 14px;
                width: 14px;
                position: absolute;
                border-radius: 50%;
                top: -4px;
                left: -6.5px;
            }

            &::before {
                content: "";
                background: url(images/graphics/bong.png);
                background-size: contain;
                width: 171px;
                height: 15px;
                position: absolute;
                bottom: -10px;
                left: -85px;
            }
        }

        &.active {
            width: 480px;
            margin-top: 0;

            .line {
                height: 232px;
                bottom: -260px;

                &::before {
                    width: 361px;
                    height: 27px;
                    position: absolute;
                    bottom: -10px;
                    left: -180px;
                }
            }

            .thumb-art {
                width: 147px;
            }
        }
    }

    .box-slide-mobile {
        display: none;
        padding: 0 0 0 15px;
        width: 100%;
        position: relative;
        padding-bottom: 20px;
        order: 1;

        .item-news {
            width: 100% !important;
            margin: 0 !important;
            border: 0 !important;

            &:before {
                display: none
            }
        }

        .swiper-container {
            padding: 0 100px 30px 0;
        }

        .item-news {
            .thumb-art {
                width: 100%;
                margin-bottom: 20px;
            }

            .content {
                overflow: visible;
            }
        }

        .swiper-pagination {
            bottom: 0px;
            display: none;
            text-align: left;

            .swiper-pagination-bullet {
                margin-right: 6px;
            }

            @media (max-width: 576px) {
                display: block
            }
        }
    }

    @media (max-width: 1791px) {
        .map {
            margin-top: 60px;
        }
    }

    @media (max-width: 1100px) {

        .map {
            order: 1;
            margin-top: 30px;
            margin-bottom: 30px;
            width: 100%;

            img {
                width: 100%;
            }
        }

        .container {
            order: 2;
            display: flex;
        }

        .item-news {
            width: 44% !important;
            margin: 0 3%;
            margin-bottom: 30px;

            .wrap {
                height: 100%;
            }

            .line {
                display: none
            }

            .thumb-art {
                width: 140px;
            }

            &.active {
                .thumb-art {
                    width: 140px;
                }
            }
        }
    }

    @media (max-width: 979px) {
        margin-bottom: 30px;

        .box-pc {
            display: none
        }

        .box-slide-mobile {
            display: block
        }
    }
}

.section-frame3 {
    max-width: 1792px;
    margin: 0 auto;
    margin-bottom: 80px;
    position: relative;

    .container {
        padding: 0 80px;
    }

    .time-line {
        background: url(images/graphics/time-line.png) no-repeat center;
        height: 4px;
        position: relative;
        background-size: cover;
        margin-bottom: -70px;
        margin-top: 120px;
    }

    .swiper-container {
        padding-top: 110px;
    }

    .swiper-slide {
        padding-left: 30px;
        position: relative;
        padding-bottom: 20px;

        &::before {
            content: "";
            border-left: 1px dashed #AA7D54;
            height: 100%;
            position: absolute;
            left: 9px;
        }

        .year {
            color: #AA7D54;
            font-size: 32px;
            font-family: var(--font1);
            position: absolute;
            top: -120px;
            left: 0;

            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background: #AA7D54;
                border-radius: 50%;
                position: absolute;
                bottom: -39px;
                left: 7px;
            }

            &::after {
                content: "";
                width: 32px;
                height: 32px;
                position: absolute;
                border: 1px solid #AA7D54;
                border-radius: 50%;
                bottom: -45px;
                left: 0;
            }
        }
    }

    .swiper-button-prev,
    .swiper-button-next {
        width: 62px;
        height: 48px;
        line-height: 48px;
        background: none;
        top: 0;
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }

    @media (max-width: 1100px) {
        .swiper-slide {
            .year {
                font-size: 24px;
                top: -108px;
            }
        }
    }

    @media (max-width: 979px) {
        display: none
    }
}

